.button {
    outline: none;
    border: none;
    border-radius: 50%;
    transition: all 0.2s ease-out 0s;
    cursor: pointer;
    font-family: "Gotham";
    font-weight: bold;
    font-size: 30px;

    height: 40px;
    width: 40px;
    display: flex;
    flex-grow: 0;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    background-color: rgb(244, 244, 244);
}

@media (orientation: portrait) {
    .button {
        height: 120px;
        width: 120px;
    }
}

.button:disabled {
    cursor: auto;
    background-color: rgb(189, 189, 189);
}

.button:hover:not(:disabled) {
    opacity: 0.7;
}

.button:active {
    opacity: 0.3 !important;
}

svg {
    margin-left: -2px;
    overflow: visible;
}

@media (orientation: portrait) {
    svg {
        height: 60px;
    }
}