.title-wrapper {
    display: flex;
    justify-content: start;
    align-items: center;

    gap: 16px;
}

.title {
    font-size: xx-large;
    font-weight: bold;
}

@media (orientation: portrait) {
    .title {
        font-size: 64px;
    }
}

.gallery {
    display: flex;
    flex-direction: column;

    row-gap: 32px;
}